import { get, set } from 'lodash';
import * as utils from './utils';

const versionField = 'ads.adEngineVersion';
const commitField = 'ads.adEngineCommit';

if (get(window, versionField, null)) {
	window.console.warn('Multiple @wikia/ad-engine initializations. This may cause issues.');
}

set(window, versionField, 'v23.12.1');
set(window, commitField, '04dec8da');
utils.logger('ad-engine', 'v23.12.1 (04dec8da)');

export * from './ad-engine';
export * from './listeners';
export * from './models';
export * from './providers';
export * from './services';
export * from './video';
export { utils };
